import React, {Component} from 'react';
import {
	BrowserRouter,
	Route,
	Switch,
	NavLink,
	Redirect
} from 'react-router-dom'
import ReactBootstrap from 'react-bootstrap';
import { Button , Grid , Col , Row , InputGroup , FormControl} from 'react-bootstrap';

import './SignUp.css';

class SignUp extends Component {
	constructor (props) {
	  super(props);
	  this.flag = ["https://alianzapacifico.net/wp-content/uploads/2015/03/2000px-Flag_of_Peru_1825_-_1950.svg_.png","data:image/jpeg;base64,","https://as01.epimg.net/img/comunes/fotos/fichas/paises/svg/chi.svg"];
	  this.id = ["Perú","México","Chile"];
	  this.code = ["+51","+56","+52"];
	  this.state = {
		selectValue:'Perú',
		flag: this.flag[0],
		code: this.code[0],
		phone: ""
	  }
	}
	
	render() {
	  const handleChange=(e)=>{
		let country = e.target.value;
		let index = this.id.indexOf(country);
		this.setState({
		  selectValue:e.target.value,
		  flag: this.flag[index],
		  code: this.code[index],
		});
	  };
	  const validatePhone = (e) => {
		const phoneNumber = e.target.value
		if (  !isNaN (phoneNumber) && phoneNumber.length<= 9) {
		  this.setState({
			phone: e.target.value,
		  });
		}
	  };
	  return (
		<div>
		  <header className="text-center">
			<div className="back">
			  <i className="fa fa-angle-left fa-3x" aria-hidden="true" />
			</div>
			<h1> Sign up </h1>
			<h4> Join now for free ride credit </h4>
			<hr />
		  </header>
		  <section className="container signUp-phone">
			<Grid>
			  <Row className="show-grid">
				<Col xs={3} md={2}>
				  <select value={this.state.selectValue} onChange={handleChange} >
					<option id="+51"  value="Perú">Perú</option>
					<option id="+56"  value="México">México</option>
					<option id="+52"  value="Chile">Chile</option>
				  </select>
				  <div>
					<img className="flag-img" src={this.state.flag}/>
				  </div>
				</Col>
				<Col xs={9}>
				  <InputGroup>
					<InputGroup.Addon>{this.state.code}</InputGroup.Addon>
					<FormControl type="tel" value={this.state.phone} placeholder={123456789} onChange={validatePhone}/>
				  </InputGroup>
				</Col>
			  </Row>
			</Grid>
		  </section>
		  <NavLink to={"/signup-form"}
			         className="btn btn-lyft btn-lg btn-block">
				Next
			</NavLink>
		</div>
	  );
	}
  }

export default SignUp;


